<template>
  <div class="section">
    <div id="char"></div>
  </div>
</template>

<script>
import echarts from 'echarts'
export default {
  data () {
    return {
    }
  },

  components: {},

  computed: {},

  mounted: function() {
    this.draw()
  },

  methods: {
    draw() {
      let char = echarts.init(document.getElementById('char'));
      let option = {
        title: {
            text: '钻井液密度曲线',
            // subtext: '数据来自西安兰特水电测控技术有限公司',
            x: 'center'
        },
        tooltip: {
            trigger: 'axis',
            axisPointer: {
                animation: true
            }
        },
       
        xAxis: [
          {
            type: 'value',
            show: false,
            max: 3
          },
          // {
          //   type: 'category',
          //   splitLine: {
          //     show: true
          //   },
          //   position: 'top',
          //   data: arr
          // }
        ],
        yAxis: [
          {
            name: '井深',
            type: 'value',
            inverse: true,
            max: 12
          }
        ],
        dataZoom: [
          {
            type: 'inside'
          },
          {
            type: 'slider'
          }
        ],
        series: [
          {
            name: '实时数据',
            data: [
              [1,1],[1.1,2],[1.1,3],[1.2,4],[1.1,5],[1.4,6],[1.2,7],[1.1,8],[1,9],[1,10]
            ],
            type: 'line',
            // xAxisIndex: 0,
            // smooth: true
          },
          {
            name: '设计数据',
            data: [
              [2,1],[2,2],[2,3],[2,4],[2,5],[2,6],[2,7],[2,8],[2,9],[2,10]
            ],
            type: 'line',
          }
          
         
        ]
      };
      char.setOption(option)
    }
  }
}
</script>

<style lang='scss' scoped>
#char {
  height: 100%;
  background: #fff;
}
</style>




